import styled from 'styled-components'

export default styled.div`
	position: fixed;
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	background-color: black;
	opacity: 0.7;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	color: white;
`


